(function($){  "use strict";  if (APP_ENV == 'production') {    console.log = function() {    };  }  $('.loader').addClass('hide');  function showLoader() {    $('.loader').removeClass('hide');    $('body').css('overflow', 'hidden');  }  function hideLoader() {    $('.loader').addClass('hide');    $('body').css('overflow', 'visible');  }  function showPage (selector) {    $(selector).removeClass('hide').siblings().addClass('hide');    facebookUtils.resizeCanvas();  }  $.ajaxSetup({    headers: {      'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')    },    error: function(jqXHR) {      if ( /<html>/i.test(jqXHR.responseText))        sweetAlert('Oups!', 'Une erreur serveur s\'est produite, veuillez réessayer ultérieurement.', 'error');      else        sweetAlert({          title: "Oups!",          text: jqXHR.responseText,          type: "error",          html: true        });    }  });})(jQuery);